<template>
    <div class="pos">
      <div>
          <el-row >
              <el-col :span='7' class="pos-order" id="order_list">  
                <div class="nav_list_left">            
                  <el-tabs>
                    <el-tab-pane label="点餐" width="100%">
                      <el-table :data="tableData" border show-summary style="width: 100%" >
                            <el-table-column prop="goodsName" label="商品"  ></el-table-column>
                            <el-table-column prop="count" label="量" width="50"></el-table-column>
                            <el-table-column prop="price" label="金额" width="70"></el-table-column>
                            <el-table-column  label="操作" width="100" fixed="right">
                                <template scope="scope">
                                    <el-button type="text" size="small" @click = "delSingGoods(scope.row)">删除</el-button>
                                    <el-button type="text" size="small" @click = "addOrderList(scope.row)">增加</el-button>
                                </template>
                            </el-table-column>
                        </el-table>
                        <div class="totalDiv">
                          <small> 数量：</small>{{totalCount}}  &nbsp;&nbsp;&nbsp;  <small>金额：</small> {{totalMoney}}元
                        </div>
                        <div class="div_btn">
                          <el-button type="warning">挂单</el-button>
                          <el-button type="danger" @click="delAllGoods()">删除</el-button>
                          <el-button type="success" @click="checkout()">结账</el-button>
                        </div>
                    </el-tab-pane>
                    <el-tab-pane label="挂单">
                      挂单
                    </el-tab-pane>
                    <el-tab-pane label="外卖">
                      外卖
                    </el-tab-pane>
                  </el-tabs>
                </div>
              </el-col>
              <!--商品展示-->
              <el-col :span="17">
                <div class="often-goods">
                  <div class="title">常用商品</div>   
                    <div class="often-goods-list">              
                      <ul>
                          <li v-for="goods in oftenGoods" @click="addOrderList(goods)">
                            <span>{{goods.goodsName}}</span>
                            <span class="o-price">￥{{goods.price}}</span>
                          </li>
                      </ul>
                    </div>
                </div>
                <!-- 下边容器 -->

                <div class="goods-type">
                  <el-tabs>
                    <el-tab-pane label="汉堡">
                        <div>
                          <ul class='cookList'>
                              <li v-for="typeGood in type0Goods">
                                  <span class="foodImg"><img :src="typeGood.goodsImg" width="100%"></span>
                                  <span class="foodName">{{typeGood.goodsName}}</span>
                                  <span class="foodPrice">￥{{typeGood.price}}元</span>
                              </li>
                          </ul>
                        </div>
                    </el-tab-pane>
                    <el-tab-pane label="小食">
                        <div>
                            <ul class='cookList'>
                                <li v-for="typeGood in type0Goods">
                                    <span class="foodImg"><img :src="typeGood.goodsImg" width="100%"></span>
                                    <span class="foodName">{{typeGood.goodsName}}</span>
                                    <span class="foodPrice">￥{{typeGood.price}}元</span>
                                </li>
                            </ul>
                        </div>
                    </el-tab-pane>
                    <el-tab-pane label="饮料">
                        <div>
                            <ul class='cookList'>
                                <li v-for="typeGood in type0Goods">
                                    <span class="foodImg"><img :src="typeGood.goodsImg" width="100%"></span>
                                    <span class="foodName">{{typeGood.goodsName}}</span>
                                    <span class="foodPrice">￥{{typeGood.price}}元</span>
                                </li>
                            </ul>
                        </div>
                    </el-tab-pane>
                    <el-tab-pane label="套餐">
                        <div>
                            <ul class='cookList'>
                                <li v-for="typeGood in type0Goods">
                                    <span class="foodImg"><img :src="typeGood.goodsImg" width="100%"></span>
                                    <span class="foodName">{{typeGood.goodsName}}</span>
                                    <span class="foodPrice">￥{{typeGood.price}}元</span>
                                </li>
                            </ul>
                        </div>
                    </el-tab-pane> 
                  </el-tabs>
                </div>
              </el-col>
          </el-row>
      </div>
    </div>
</template>

<script>
import axios from 'axios'
export default {
  name: 'Pos',
  // 运用钩子函数用js更改框架的原生高度
  data(){
    return {
      tableData: [],
      oftenGoods:[],
      type0Goods:[],
      type1Goods:[],
      type2Goods:[],
      type3Goods:[],
      totalMoney:0,
      totalCount:0
    }
  },
  created() {
      axios.get('https://www.easy-mock.com/mock/5b8b30dbf032f03c5e71de7f/kuaican/oftenGoods')
      .then(response =>{
        console.log(response,"后台传来的数据")
        this.oftenGoods = response.data
      })
      .catch(error =>{
        console.log(error)
        alert('网络错误，不能访问')
      })
  },
  mounted() {
    var orderHeight = document.body.clientHeight
    console.log(orderHeight)
    document.getElementById('order_list').style.height = orderHeight + 'px' 
  },
  methods: {
    addOrderList(goods){
            // this.totalCount=0;
            // this.totalMoney=0;
            this.totalCount=0; //汇总数量清0
            this.totalMoney=0;
            let isHave=false;
            //判断是否这个商品已经存在于订单列表
            for (let i=0; i<this.tableData.length;i++){
                if(this.tableData[i].goodsId==goods.goodsId){
                    isHave=true; //存在
                }
                console.log(isHave);
            }
            //根据isHave的值判断订单列表中是否已经有此商品
            if(isHave){
                //存在就进行数量添加
                 let arr = this.tableData.filter(o =>o.goodsId == goods.goodsId);
                 arr[0].count++;
                 //console.log(arr);
            }else{
                //不存在就推入数组
                let newGoods={goodsId:goods.goodsId,goodsName:goods.goodsName,price:goods.price,count:1};
                 this.tableData.push(newGoods);
            }
            this.getAllMoney()            
      },
      delSingGoods(goods){
        console.log(goods)
        this.tableData = this.tableData.filter(o => o.goodsId != goods.goodsId)
        this.getAllMoney()
      },
      delAllGoods(){
        this.tableData = []
        this.totalCount = 0
        this.totalMoney = 0
      },
       //进行数量和价格的汇总计算
      getAllMoney(){
        this.totalCount = 0
        this.totalMoney = 0
        if (this.tableData){
          this.tableData.forEach((element) => {
                this.totalCount+=element.count;
                this.totalMoney=this.totalMoney+(element.price*element.count);   
            });  
        }
      },
      // 模拟结账
      checkout() {
        if(this.totalCount != 0){
          this.tableData = []
          this.totalCount = 0
          this.totalMoney = 0
          this.$message({
            message: '结账成功，感谢你又为店里出了一份力!',
            type: 'success'
          });
        }else {
          this.$message.error('不能空结。老板了解你急切的心情！');
        }
      }
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.pos-order{
  background-color: #F9FAFC;
  border-right: 1px solid #C0CCDA;
}
.nav_list_left{
  margin:10px;
}
.div_btn{
  margin-top: 20px;
}
.title{
  height: 20px;
  border-bottom:1px solid #D3DCE6;
  background-color: #F9FAFC;
  padding:10px;
}
.often-goods-list ul li{
  list-style: none;
  float:left;
  border:1px solid #E5E9F2;
  padding:10px;
  margin:5px;
  background-color:#fff;
}
.o-price{
  color:#58B7FF; 
}
.goods-type{
  clear: both;
}
.cookList li{
       list-style: none;
       width:23%;
       border:1px solid #E5E9F2;
       height: auot;
       overflow: hidden;
       background-color:#fff;
       padding: 2px;
       float:left;
       margin: 2px;
 
   }
.cookList li span{
       
        display: block;
        float:left;
   }
.foodImg{
       width: 40%;
   }
.foodName{
       font-size: 18px;
       padding-left: 10px;
       color:brown;
 
   }
.foodPrice{
       font-size: 16px;
       padding-left: 10px;
       padding-top:10px;
   }
.totalDiv{
  background-color: #fff;
  padding: 10px;
  border-bottom: 1px solid #D3DCE6;
}
</style>



